.button{
    font: inherit;
    border: 1px solid #4f005f;
    background: #4f005f;
    color: white;
    padding: 0.25rem 1rem;
    cursor: pointer;
    width: 7rem;
    text-align: center;
}

.button:hover,.button:active{
    background-color: #741188;
    border-color: #741188;
}

.button:focus{
    outline: none;
}
